.createPostPage {
    font-family: Arial, Helvetica, sans-serif;
    width: 100vw;
    height: 70vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .formContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 500px;
    height: auto;
    padding: 20px;
    border: 5px solid rgb(80, 199, 246);
    border-radius: 5px;
  }
  
  .formContainer #create {
    height: 40px;
    margin-top: 10px;
    margin-bottom: 10px;
    /* border: 2px solid grey; */
    border-radius: 5px;
    padding-left: 10px;
    font-size: 20px;
  }
  
  .formContainer button {
    margin-top: 15px;
    height: 40px;
    border: none;
    background-color:rgb(80, 199, 246);
    border-radius: 5px;
    color: white;
  }
  
  .formContainer button:hover {
    cursor: pointer;
    color:white;
    background-color: rgb(0, 0, 0);
    border-radius: 30px;
  }
  
#erroe {
